  <div class="bootstrap">
    <!-- if dev environment show homeNavBar -->

    <NavBar v-if="isDevelopment()" :jsonData="data" />

    <!-- show main application -->
    <LandingPage v-if="!isDevelopment()" />

  </div>

</template>

<script>
import NavBar from './components/organisms/NavBar';
import LandingPage from './pages/LandingPage';

export default {
	name: 'App',
	components: { NavBar, LandingPage },
	methods: {
		isDevelopment() {
			if( Object.keys(this.$route.query).includes("development") )
				return true;

			return false;
		}
	},
	beforeMount() {
		if( typeof window.payplug_admin_config !== "undefined"){
			// eslint-disable-next-line no-undef
			__webpack_public_path__ = window.payplug_admin_config.img_path;
			console.log("--->");
		}

		console.log(window.payplug_admin_config);


	}
}
</script>

